.wrapper {
    max-width: 1400px;
    min-width: 960px;
    margin: 0 auto;
}

body {
    font-size: 16px;
    line-height: 1.4;
}

.row, .container-fluid, .container, section, footer, aside {
     max-width: 100%;
}
.panel-info {
    border: 1px solid #ccc;
    /*padding: 25px 15px 15px;*/
    border-radius: 5px;
    position: relative;
}

.panel-info h3 {
    padding: 25px 15px 0;
}

.panel-info__close {
    cursor: pointer;
    position: absolute;
    right: 10px;
    top: 5px;
    font-size: 20px;
}

.panel-info__close:before {
    content: "\2716";
}

.panel-info h4 {
    margin-top: 10px;
}

.panel-info .question-list li:last-child {
    margin: 0;
}


.text-right {
    float: right;
}

.cursor-default {
    cursor: default;
}
.h1, .h2, .h3, h1, h2, h3 {
    margin-top: 20px;
    margin-bottom: 25px;
}
.spinner-block {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
    background: rgba(255, 255, 255, 0.45);
}

.spinner {
    position: absolute;
    width: 120px;
    height: 120px;
    top: 50%;
    left: 50%;
    margin: -60px 0 0 -60px;
}

.spinner:before,
.spinner:after {
    content: "";
    display: block;
    position: absolute;
    border-width: 4px;
    border-style: solid;
    border-radius: 50%;
}

.spinner:before {
    width: 100%;
    height: 100%;
    border-bottom-color: #000;
    border-right-color: #000;
    border-top-color: transparent;
    border-left-color: transparent;
    top: 0;
    left: 0;
    -webkit-animation: rotate-animation 1s linear 0s infinite;
            animation: rotate-animation 1s linear 0s infinite;
}

.spinner:after {
    width: 60%;
    height: 60%;
    border-bottom-color: #000;
    border-right-color: #000;
    border-top-color: transparent;
    border-left-color: transparent;
    top: 20%;
    left: 20%;
    -webkit-animation: anti-rotate-animation 0.85s linear 0s infinite;
            animation: anti-rotate-animation 0.85s linear 0s infinite;
}

@-webkit-keyframes rotate-animation {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes rotate-animation {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@-webkit-keyframes anti-rotate-animation {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(-360deg);
                transform: rotate(-360deg);
    }
}

@keyframes anti-rotate-animation {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(-360deg);
                transform: rotate(-360deg);
    }
}
.nav.nav-pills {
    padding: 5px;
    background: #fafafa;
}
.search-block {
    margin-top: 200px;
}
.answer-list {
    margin: 0;
    padding: 0;
    list-style: none;
}

.answer-list li {
    position: relative;
    border-top: 1px solid #e1e1e1;
    border-bottom: 1px solid #e1e1e1;
    padding: 15px 15px 5px;
    margin: 0 0 15px;
    background: #fafafa;
}

.answer-list .accepted {

}

.answer-list .accepted:before {
    position: absolute;
    top: 0;
    left: -40px;
    font-size: 35px;
    color: #5fba7d;
    content: "\2714";

}

.answer-list .answer-info {
    margin-top: 10px;
    color: #aaa;
    font-size: 14px;
}
.question-list {
    margin: 0;
    padding: 0;
    list-style: none;
}

.question-item {
    margin: 0 0 25px;
    background: #fafafa;
    padding: 15px;
    border-radius: 5px;
}

.question-item:hover {
    background: #f6f6f6;
}

.question-item__title {
    font-size: 20px;
    word-break: break-word;
}

.question-item__author span {
    cursor: pointer;
}

.question-item__author.cursor-default span {
    cursor: default;
}

.question-item__author,
.question-item__date {
    color: #aaa;
    font-size: 14px;
    margin-bottom: 3px;
}

.tags {
    margin: 11px 0 0;
    padding: 0;
    list-style: none;
}

.tags li {
    display: inline-block;
    margin-right: 8px;
    cursor: pointer;
    margin-bottom: 6px;
}

.tags.cursor-default button {
    cursor: default;
}
.question-info {}

.question-info__owner {
    margin: 15px 0;
    color: #a4a4a4;
}
